const cards = beers.cards

function Cardcontainer() {
  return (
    <div className="card-container">
        {cards.map(({title, sub, text}, index) => <Beercard 
            key={index}
            title={title}
            sub={sub}
            text={text}
            index={index+1}
            />)
        }
    </div>
  )
}

export default Cardcontainer